
.rankings-container {
  border: 2px solid white;
  border-radius: 15px;
  padding: 20px;
  padding-left: 50px; /* Increase this value to move content more towards the center */
  margin: 0 auto;
  width: 70%; /* Adjust this to control the width of your box */
  max-width: 800px;
  background-color: white;
  color: black;
  font-family: 'Roboto', sans-serif;
}

@media only screen and (max-width: 600px) {
  .rankings-container {
    width: 90%; 
  }
}



