.card {
  width: 300px;
  height: 400px;
  position: relative;
  perspective: 1000px;
  margin: 20px auto;
}

.card .card-front,
.card .card-back {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  background: #fff;
  transition: transform 0.6s;
  backface-visibility: hidden;
  overflow: auto; /* Added this to handle overflow */
  word-wrap: break-word; /* Added this to ensure long words don't extend beyond the card */
}


.card .card-front {
  z-index: 2;
  transform: rotateY(0deg);
}

.card .card-back {
  transform: rotateY(180deg);
}

.card.flipped .card-front {
  transform: rotateY(-180deg);
}

.card.flipped .card-back {
  transform: rotateY(0deg);
}

.view-more-btn,
.view-less-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background: #007bff;
  color: #fff;
  cursor: pointer;
}

.ranking-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}
